.backdrop {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.3);
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 10;
  }
  .backdrop.is-hidden {
    opacity: 0;
    pointer-events: none;
  }
.modal-ingredients-group{
    overflow-y: scroll;
    height: 673px;
    position: relative;
    background-color: #FFFFFF;
    padding: 20px;
   @include media-tablet-min(768px) {
    width: 715px;
    height: 557px;
    padding: 28px;
    &::after {
     position: absolute;
  top: 169px;
     left: 0;
     content: "";
     width: 100%;
     height: 1px;
     background-color: #FD5103;
   }
  }
  &::-webkit-scrollbar {
    width: 1px;
    height: 4px;
    background-color: #FFFFFF;
}
}
.modal-ingredient-title{
    font-family: 'Roboto';
    margin-bottom: 20px;
font-style: normal;
font-weight: 900;
font-size: 32px;
line-height: 1.1;
color: #202025;
padding-top: 12px;
&:hover,
&focus {
  color: #fd5103;
}
@include media-tablet-min(768px) {
    margin-bottom: 28px;
  }
}
.modal-ingredient-uptitle{
    font-family: 'Poppins';
font-weight: 600;
font-size: 16px;
line-height: 1.5;
text-transform: uppercase;
color: #202025;
}
.modal-ingredient-text{font-style: normal;
    font-family: 'Poppins';
    font-weight: 400;
    font-size: 16px;
    line-height: 1.75;
    margin-top: 20px;
    color: #5F6775;
    @include media-tablet-min(768px) {
        margin-top: 68px;
      }}
.modal-ingredient-list{
    margin-top: 28px;
    display: flex;
    flex-direction: column;
    margin-bottom: 32px;
    @include media-tablet-min(768px) {
        margin-bottom: 40px;
      }
}
.modal-ingredients-close{
    position: absolute;
    background-color: inherit;
    border: none;
    right: 0;
    top: 0;
    padding: 16px 16px;
    @include media-tablet-min(768px) {
      padding: 12px 12px;
         }
}
.modal-btn__add-to-fav,
.modal-btn__remove-fav{
    margin-left: 0;
    margin-bottom: 20px;
   @include media-tablet-min(768px) {
margin-right: 0;
margin-left: auto;
   }
}

 .modal-ingredients-close-icon{
    &:hover,
    &focus {
      fill: #fd5103;
    }
 }